@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-weight: 800;
    letter-spacing: 0.05em;
    color: white;
    font-family: 'Roboto Slab', serif;
}

body {
    background-image: "./components/images/nasa.jpg";
    background-color: rgb(0, 0, 74);
}

#skillsComponent {
    position: relative;
}

.navbar-toggler-icon {
    color: black;
}

#projectComponent {
    background-color: white;
}


.projects {
    background-size: contain;
    padding-bottom: 20px;
}

.projectCard {
    display: flex;
    justify-content: center;
    padding: 10px;
    border: 2px solid black;
}

.project {
    opacity: 0.9;
}

.project:hover {
    opacity: 1;
}

.btn {
    font-weight: 800;
    letter-spacing: 0.05em;
}

.card {
    width: auto;
    margin-top: 10px;
    margin-bottom: 10px;
}

.card p,
li,
h5 {
    color: black;
    font-weight: 700;
}

.card-body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card-body p {
    color: rgb(0, 50, 186);
    text-align: center;
}

.card-title {
    font-weight: 900;
    color: rgb(0, 18, 181);
    text-decoration: underline;
}

.list-group-item {
    text-align: center;
}

.fa-react {
    color: rgb(0, 120, 172)
}

.fa-gem {
    color: red;
}

.fa-database {
    color: rgb(53, 53, 53);
}

.fa-node-js {
    color: rgb(0, 172, 6);
}

.fa-html5 {
    color: rgb(238, 72, 0);
}

.fa-css3-alt {
    color: blue;
}

.jquery {
    width: 15px;
    height: 16px;
}

.skills {
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: white;
    color: black;
}

.skills-title {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
}

.skills-title i {
    color: rgb(2, 2, 167);
    padding-left: 10px;
    padding-right: 10px;
}


.skills-title h1 {
    color: rgb(2, 2, 167);
}

.skillProfile {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.language-container {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
    margin-bottom: 20px;
    -webkit-filter: drop-shadow(5px 5px 2px #bdbdbd);
            filter: drop-shadow(5px 5px 2px #bdbdbd);
}

.language-circle {
    border-radius: 50%;
    /* background-color: rgb(0, 145, 255); */
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
}

.language {
    width: 60px;
    height: 60px;
    z-index: 2;
}

.skilltext {
    font-size: 1.2em;
    z-index: 1;
    display: flex;
    align-items: center;
    color: rgb(0, 81, 255);

}

.projects-title {
    background-color: white;
    margin-top: 50px;
    margin-bottom: 20px;
}

/* Nav */
.navbar>.container-fluid {
    justify-content: center;
}

.navUL {
    display: flex;
    justify-content: center;
}

.menu-bar {
    border-radius: 25px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: inline-flex;
    background-color: rgba(0, 0, 0, .4);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    align-items: center;
    padding: 0 10px;
}

.menu-bar li {
    list-style: none;
    color: white;
    font-family: sans-serif;
    font-weight: bold;
    padding: 12px 16px;
    margin: 0 8px;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
}

.menu-bar li::before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    transition: .2s;
    border-radius: 25px;
}

.menu-bar li:hover {
    color: black;
}

.menu-bar li:hover::before {
    background: linear-gradient(to bottom, #e8edec, #d2d1d3);
    box-shadow: 0px 3px 20px 0px black;
    transform: scale(0.9);
}

a {
    color: white;
}
